<template>
  <section>
    <div class="weather_detail">
      <div class="container mt-4">
        <div class="row">
          <div class="col-md-6 col-sm-12">
            <h1 class="weather_place">
              {{place.siteName}}
              <select v-model="areaWeather" @change="weatherselectarea($event)">
                <option
                  v-for="(site, index, w_key) in selectSite"
                  :value="site.id"
                  :key="w_key"
                >{{site.name}}</option>
              </select>
            </h1>
          </div>
          <div class="col-md-6 col-sm-12">
            <div>
              <div class="weather_today_icon_out">
                <span v-if="weatherData.desc === '多雲' ">
                  <font-awesome-icon class="weather_today_icon" size="1x" icon="cloud"/>
                </span>
                <span v-else-if="weatherData.desc === '陰' ">
                  <font-awesome-icon
                    class="weather_today_icon"
                    size="1x"
                    icon="cloud"
                    style="color:#e4e4e4"
                  />
                </span>
                <span v-else-if="weatherData.desc === '晴' ">
                  <font-awesome-icon class="weather_today_icon" size="1x" icon="sun"/>
                </span>
                <span v-else-if="weatherData.desc === '短暫雨' ">
                  <font-awesome-icon
                    class="weather_today_icon"
                    size="1x"
                    icon="cloud-showers-heavy"
                  />
                </span>
                <span v-else>{{weatherData.desc}}</span>
              </div>
            </div>
          </div>
          <div class="col-12" v-if="weatherData.specials != undefined ">
            <div class="main" v-for="special in weatherData.specials" :key="special">
              <div class="main_title">
                <h2>天氣特報 - {{special.title}}</h2>
              </div>
              <div>
                <p>{{special.desc}}</p>
              </div>
            </div>
          </div>
          <div v-else></div>
          <div class="col-12">
            <div class="main">
              <div class="main_title">
                <h2>歷史天氣</h2>
              </div>
              <!-- icon croup -->
              <div
                class="weather_detail_card"
                v-for="(history, index, his_key) in weatherData.histories"
                :key="his_key"
              >
                <div class="weather_detail_icon">
                  <span v-if="history.desc === '多雲' ">
                    <font-awesome-icon size="2x" icon="cloud"/>
                  </span>
                  <span v-else-if="history.desc === '陰' ">
                    <font-awesome-icon
                      class="weather_today_icon"
                      size="2x"
                      icon="cloud"
                      style="color:#e4e4e4"
                    />
                  </span>
                  <span v-else-if="history.desc === '晴' ">
                    <font-awesome-icon class="weather_today_icon" size="2x" icon="sun"/>
                  </span>
                  <span v-else-if="history.desc === '短暫陣雨' ">
                    <font-awesome-icon
                      class="weather_today_icon"
                      size="2x"
                      icon="cloud-showers-heavy"
                    />
                  </span>
                  <span v-else>{{history.desc}}</span>
                </div>
                <div class="weather_detail_celsius">
                  <h2 class="mb-0">{{history.temperature}}℃</h2>
                </div>
                <div class="weather_detail_day">{{history.at}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  name: "weather",
  computed: {
    dataWeather: {
      get() {
        return this.weatherData;
      },
      set(val) {
        this.$emit("update:weatherData", val);
      }
    },
    selectSite() {
      return this.siteData[this.place.siteNum - 1].towns;
    },
    areaWeather: {
      get() {
        return this.place.areaNum;
      },
      set(val) {
        let areaTowns = this.siteData[this.place.siteNum - 1].towns;
        let Home_areaName = "";
        areaTowns.filter((item) => {
          if (val === item.id) {
            return (Home_areaName = item.name);
          }
        });
        this.$emit("update:place", {
          siteNum: Number(this.siteData[this.place.siteNum - 1].id),
          siteName: String(this.siteData[this.place.siteNum - 1].name),
          areaNum: Number(val),
          areaName: String(Home_areaName)
        });
      }
    }
  },
  methods: {
    weatherselectarea(event) {
      if (event.target.value === "") {
        this.areaWeather = this.siteData[this.place.siteNum - 1].towns[0].id;
      }

      let d = new Date().getTime();
      let weather_url = `https://works.ioa.tw/weather/api/weathers/${
        this.areaWeather
      }.json?_=${d}`;
      this.axios({
        method: "get",
        url: weather_url
      }).then(res => {
        this.dataWeather = res.data;
      });
    }
  },
  props: {
    showData: {
      type: Array,
      required: true
    },
    siteData: {
      type: Array,
      required: true
    },
    place: {
      type: Object,
      required: true
    },
    weatherData: {
      type: Object,
      required: true
    }
  },
  mounted() {
    let d = new Date().getTime();
    let weather_url = `https://works.ioa.tw/weather/api/weathers/${
      this.areaWeather
    }.json?_=${d}`;
    this.axios({
      method: "get",
      url: weather_url
    }).then(res => {
      (this.Loading = true), (this.dataWeather = res.data);
    });
  }
};
</script>
